  .productImage{
    width: 100%;
    max-width: 80px;
    margin-right: 10px;
  
    border: 2px solid transparent;
    border-radius: 10px;
    cursor: pointer;
  
  }

  .selectedProduct{
    border: 2px solid teal;
  }

  .featuredButtons{
    min-width: 80px;
    background-color: gainsboro;
    border-radius: 8px;
    border: 0;
    padding: 12px 24px;
    cursor: pointer;
    margin-right: 10px;
    box-sizing: content-box;
    font-weight: 600;
  }
  
  .selectedfeaturedButtons{
    color: white;
    background-color: gray;
  }
  
  .purchaseButton{
    color: white;
    background-color: teal;
    cursor: pointer;
    padding: 12px 24px;
    border-radius: 10px;
    border: 0;
  
    margin-top: 30px;
    min-width: 80px;
    box-sizing: content-box;
    font-weight: 600;
  }