.productPreview{
    width: 100%;
    position: relative;
  }
  
  .productPreview > img{
    width: 100%;
  }


.productFeaturedTimeData{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  
    color: white;
    font-weight: 600;
    font-size: 35px;
  }
  
  .productFeaturedHeartData{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  
    color: white;
    font-weight: 600;
    font-size: 35px;
  }
  
  .productFeaturedHeartData > i {
    color: red;
    animation: beat 1s infinite;
  }
  
  .productFeaturedHeartData > p {
    margin-top: 16px;
  }
  
  @keyframes beat {
    0%{
      transform: scale(1, 1);
    }
  
    50%{
      transform: scale(1.2, 1.2);
    }
  
    100%{
      transform: scale(1, 1);
    }
  }

  
